<template>
  <div class="login text-center ">
    <div class="bg-primary-gradient">
      <div class="logo">
        <v-img
            :src="require('../assets/logo-white.png')"
            contain
            height="176"
            class="mb-5"
        />
      </div>
      <div class="title-primary">
        <div class="title">Recuperar Contraseña</div>
        <div class="subtitle">Ingresa el correo electronico<br> registrado en tu cuenta LegalHelp</div>
      </div>

      <div style="padding: 0 3rem">
        <v-text-field
            v-model="email"
            label="Correo"
            solo
            append-icon="mdi-lock"
            color="white"
            background-color="white"
        ></v-text-field>
      </div>

      <div class="my-5">
        <v-btn width="240" :loading="isLoading" @click="onSend">Continuar</v-btn>
      </div>
      <div>
        <v-btn width="240" @click="goToBack">Regresar</v-btn>
      </div>
    </div>
    <svg width="100%" height="124" viewBox="0 0 100 100" preserveAspectRatio="none">
      <defs>
        <polygon id="r1" points="0,0 100,0 70.8,100 0,22.5"/>
        <linearGradient id="g1" x1="0%" y1="0%" x2="100%" y2="0%">
          <stop stop-color="#0084CA" offset="0%"/>
          <stop stop-color="#00406C" offset="100%"/>
        </linearGradient>
      </defs>
      <use x="0" y="0" xlink:href="#r1" fill="url(#g1)"/>
    </svg>
  </div>
</template>
<script>
import axios from 'axios';

export default {
  name: 'Password',
  inject: ['notyf'],
  data: () => ({
    isLoading: false,
    email: '',
  }),
  created() {

  },
  methods: {
    onSend() {
      if (this.email === '') {
        this.notyf.error('Correo electronico no valido.')
        return
      }

      this.isLoading = true
      axios.post('/auth/password', {email: this.email})
          .then(() => {
            this.notyf.success('Se le envio un correo electronico con las indicaciones.')
          })
          .catch((error) => {
            this.notyf.error(error.response.data.message)
          })
          .then(() => {
            this.isLoading = false
          })
    },
    goToBack() {
      this.$router.push({name: 'Login'})
    },
  }
}
</script>

<style lang="scss" scoped>
.login {
  color: white;

  .logo {
    padding-top: 10%;
  }

  .title-primary {
    padding: 2rem 0;
  }
}
</style>